<template>
  <div id="tabletRegister">
    <img src="../../assets/circlesMark_images/rllogo.png" alt="">
    <ul>
      <li>感谢您对青海省生态保护红线的关注</li>
      <li>您所查看的是位于{{data.placeName}}的界碑界桩</li>
      <li>它的编号为{{data.jzNumber}}</li>
      <li>想了解更多生态保护红线的信息</li>
      <li>请点击下方按钮</li>
    </ul>
    <button>了解详情</button>
  </div>
</template>

<script>
import 'lib-flexible'
import { getMarkerByCoordinate } from '@/api/acquisitionTable'
import { renderTime } from '@/utils/common'
export default {
  name: 'TabletRegister',
  data () {
    return {
      /* 获取的数据 */
      data: {
        lmMarkerRelationPositions: [],
        lmMarkerPhotos: [],
        lon: '',
        lat: ''
      }
    }
  },
  computed: {
    /* 处理界桩现场照片数据 */
    lmPhotos: function () {
      let photosData = [{}, {}, {}, {}, {}, {}]
      for (let i in photosData) {
        for (let j in this.data.lmMarkerPhotos) {
          if (this.data.lmMarkerPhotos[j].type === i * 1 + 1) {
            photosData[i] = this.data.lmMarkerPhotos[j]
          }
        }
        if (photosData[i] === '') {
          photosData[i].url = ''
        }
      }
      let arr1 = [photosData[5]]
      photosData = arr1.concat(photosData)
      photosData.pop()
      return photosData
    }
  },
  props: {
    tabletId: {}
  },
  watch: {
  },
  created () {
    this.getLonLat()
  },
  mounted () {
  },
  methods: {
    /* 获取当前经纬度 */
    getLonLat () {
      if (this.$route.query.lon === undefined || this.$route.query.lat === undefined) {
        this.getLocation()
      } else {
        this.lon = this.$route.query.lon
        this.lat = this.$route.query.lat
        this.getTableData()
      }
    },
    /* http://localhost:8080/#/tabletMove?lon=38.1776&lat=38.1776 */
    /* 获取原始界桩列表  38.1776,38.1776 */
    getTableData () {
      getMarkerByCoordinate({
        data: {
          longitude: this.lon * 1,
          latitude: this.lat * 1
        }
      }).then((res) => {
        this.data = res.data.data
      }).catch(() => {
        this.$Message.error('服务器错误')
      })
    },
    /* 方位物数据处理 */
    lmMPositionsDate (lmMarkerRelationPositions) {
      if (lmMarkerRelationPositions.length < 3) {
        return lmMarkerRelationPositions.push({})
      } else {
        return lmMarkerRelationPositions.slice(0, 3)
      }
    },
    /* 日期格式转换 */
    renderTime (date) {
      return renderTime(date)
    },
    /* 123456转东南西北中 */
    changeImgType (type) {
      switch (type) {
        case 1:
          return '东'
        case 2:
          return '西'
        case 3:
          return '南'
        case 4:
          return '北'
        case 5:
          return '中'
        case 6:
          return ''
        default:
          return ''
      }
    },
    getLocation () { // 检测是否支持地理定位 如果支持，则运行 getCurrentPosition() 方法。如果不支持，则向用户显示一段消息。
      if (navigator.geolocation) { // 如果getCurrentPosition()运行成功， //则向参数showPosition中规定的函数返回一个coordinates对象
        navigator.geolocation.getCurrentPosition((position) => {
          this.lon = position.coords.longitude
          this.lat = position.coords.latitude
          this.getTableData()
        }, error => {
          switch (JSON.stringify(error.code)) {
            case 1:
              this.$Message.error('地理位置信息的获取失败，因为该页面没有获取地理位置信息的权限。')
              break
            case 2:
              this.$Message.error('地理位置获取失败，因为至少有一个内部位置源返回一个内部错误。')
              break
            case 3:
              this.$Message.error('获取地理位置的超时时长。')
              break
            default:
              this.$Message.error('地理位置信息的获取失败，因为该页面没有获取地理位置信息的权限。')
          }
        })
      } else {
        this.$Message.error('对不起，您的浏览器不支持地理位置定位')
      }
    },
    getwixinLocation () {
      let wx = require('weixin-js-sdk')
      wx.config({
        debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: '', // 必填，公众号的唯一标识
        timestamp: '', // 必填，生成签名的时间戳
        nonceStr: '', // 必填，生成签名的随机串
        signature: '', // 必填，签名
        jsApiList: [] // 必填，需要使用的JS接口列表
      })
      wx.getLocation({
        type: 'wgs84',
        success: (res) => {
          this.lat = res.latitude
          this.lon = res.longitude
          this.getTableData()
        },
        error: (error) => {
          this.$Message.error(JSON.stringify(error))
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#tabletRegister{
  width: 100%;
  height: 100%;
  background: url("../../assets/circlesMark_images/tableMove_back.jpg") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  img{
    width: 5.5rem;
    margin-top: 1.95rem;
  }
  ul{
    margin-top: 1.62rem;
    color:#fff;
    li{
      font-size: 0.28rem;
      line-height: 0.54rem;
    }
  }
  button{
    background: url("../../assets/circlesMark_images/tableMove_btn.png");
    background-size: 100% 100%;
    border: 0;
    padding: 0.19rem 0.43rem;
    color:#fff;
    font-size: 0.28rem;
    font-weight: bolder;
    outline: none;
    margin-top: 0.86rem;
    cursor: pointer;
    box-shadow: 0 0.1rem 4px 0px rgba(0, 0, 0, 0.1);
  }
}
</style>
